body, h1, h2, h3, h4, h5, h6, hr, p, blockquote, dl, dt, dd, ul, ol, li, pre, form, fieldset, legend, button, input, textarea, th, td { margin:0; padding:0; } 
body, button, input, select, textarea { font:12px/1.5tahoma, arial, \5b8b\4f53; } 
h1, h2, h3, h4, h5, h6{ font-size:100%; } 
address, cite, dfn, em, var { font-style:normal; } 
code, kbd, pre, samp { font-family:couriernew, courier, monospace; } 
small{ font-size:12px; } 
ul, ol { list-style:none; } 
a { text-decoration:none; } 
a:hover { text-decoration:underline; cursor: pointer;} 
sup { vertical-align:text-top; } 
sub{ vertical-align:text-bottom; } 
legend { color:#000; } 
fieldset, img { border:0; } 
button, input, select, textarea { font-size:100%; } 
body{background: #f8f8f8;}
#nav{
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	height: 90px;
	line-height: 90px;
	background: #f8f8f8;
	z-index: 9999;}

.nav-wrap{
	position: relative;
	margin: auto;
	padding: 0 16px;
	width: 1200px;}

.nav-logo{
	position: relative;
	display: inline-block;
	width: 60px;
	height: 60px;
	vertical-align: middle;
	border: 1px solid;
	border-radius: 30px;} 

.nav-info{
	display: inline-block;
	list-style: none;}

.nav-info li{
	display: inline-block;
	margin-left: 32px;
	font-size: 16px;}
.main-wrap {
	position: relative;
	margin: 90px auto 0;
	padding: 0 32px;
	width: 1200px;
	background: #fff;}
.list-item{
	height: 125px;
	border-bottom: 1px solid #aaa;
	padding: 16px 0;
	overflow: hidden;
	color: #4a4a4a;}

.item-title{
	height: 32px;
	padding-left: 35px;
	line-height: 32px;
	font-size: 18px;
	color: #000;
	cursor: pointer;
	background-image: url(/static/media/book.48df608f.svg);
	background-size: 25px 25px;
	background-position: 0 6px;
	background-repeat: no-repeat;}

.item-des{
	margin-top: 12px;
	font-size: 16px;}

.item-info{
	margin-top: 12px;
	float: right;}

.space-line{
	display: inline-block;
	margin-left: 12px;
	height: 12px;
	width: 9px;
	border-left: 1px solid #aaa;}

.item-info .type{
	padding-left: 24px;
	cursor: pointer;
	background-image: url(/static/media/type.24374018.svg);
	background-size: 14px 14px;
	background-position: 5px 5px;
	background-repeat: no-repeat;}

.item-info .read{
	padding-left: 24px;
	background-image: url(/static/media/read.9fffe4b1.svg);
	background-size: 16px 16px;
	background-position: 4px 4px;
	background-repeat: no-repeat;}
.toggle-bar{
	padding:  32px 0;
	overflow: hidden;}

.toggle-bar-wrap{
	position: relative;
	display: inline-block;	
	left: 50%;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);}

.toggle-bar-wrap span{
	display: inline-block;
	margin-left: 12px;
	height: 32px;
	width: 32px;
	border: 1px solid #e1e2e3;
	text-align: center;
	line-height: 32px;
    cursor: pointer;	}

.toggle-bar-wrap span:first-child{
	width: 60px;	
}

.toggle-bar-wrap span:last-child{
	width: 60px;	
}
